  padding: 82px 0 152px;
}
.article h1 {
  line-height: 0.85;
}
.article__tag {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.article__tag p {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #5d98b9;
  padding: 4px 12px;
  border: 1px solid #bdd9e8;
  border-radius: 55px;
}
.article__content {
  margin: 28px 0 0;
}
.article__content *:nth-child(1) {
  margin: 0;
}
.article__content h3 {
  font: 500 25px/1.3 "SF Pro Display", sans-serif;
  color: #0082c9;
  margin: 38px 0 0;
  max-width: 745px;
}
.article__content p {
  margin: 26px 0 0;
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
  max-width: 745px;
}
.article__content p + p {
  margin: 20px 0 0;
}
.article__content img {
  width: 100%;
  box-shadow: 0 0 0 0 rgba(120, 120, 120, 0.02), 0 2px 5px 0 rgba(120, 120, 120, 0.02), 0 10px 10px 0 rgba(120, 120, 120, 0.02), 0 22px 13px 0 rgba(120, 120, 120, 0.01), 0 39px 16px 0 rgba(120, 120, 120, 0), 0 61px 17px 0 rgba(120, 120, 120, 0);
  border-radius: 20px;
  margin: 43px 0 0;
}
.article__content img + p {
  font: 400 16px/1.3 "SF Pro Display", sans-serif;
  color: #77aecc;
  margin: 15px 0 0;
}
.article .container__pad {
  margin-bottom: 150px;
}

@media (max-width: 1024px) {
  .article {
    padding: 40em 0 75em;
  }
  .article h1 {
    line-height: 0.95;
  }
  .article .container__pad {
    margin-bottom: 72em;
  }
  .article__tag {
    gap: 8em;
  }
  .article__tag p {
    font-size: 11em;
    line-height: 1.2;
    padding: calc(var(--gum) * 6) calc(var(--gum) * 10);
  }
  .article__content {
    margin: 16em 0 0;
  }
  .article__content h3 {
    font-size: 19em;
    margin: calc(var(--gum) * 22) 0 0;
    line-height: 1.2;
  }
  .article__content p {
    font-size: 13em;
    margin: calc(var(--gum) * 15) 0 0;
  }
  .article__content p + p {
    margin: calc(var(--gum) * 14) 0 0;
  }
  .article__content img {
    margin: 25em 0 0;
    border-radius: 20em;
  }
  .article__content img + p {
    margin: calc(var(--gum) * 11) 0 0;
    font-size: 12em;
  }
}
@media (max-width: 600px) {
  .article {
    padding: 63em 0 74em;
  }
  .article h1 {
    letter-spacing: calc(var(--gum) * -3);
    line-height: 0.85;
  }
  .article .container__title {
    display: none;
  }
  .article .container__pad {
    margin-bottom: 74em;
  }
  .article__content {
    margin: 20em 0 0;
  }
  .article__content img {
    margin: 23em 0 0;
    height: 290em;
    object-fit: cover;
  }
}
.curtain {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: #0082c9;
  transform: translatex(0%);
  transition: transform 0.9s cubic-bezier(0.76, 0, 0.24, 1) 0.5s;
  will-change: transform;
}
.curtain.active {
  transform: translateX(100%);
}